    searchPostulante() {

      if (
        this.postulante.cedula_dni == "" &&
        this.postulante.fecha_nacimiento == ""
      ) {
        this.toast({
          color: "info",
          text: "Ingrese Cédula de identidad y Fecha de nacimiento",
        });
      } else {
        this.datosBuscados = true;
        this.search_loading = true;
        this.postulante.nombres = "";
        this.postulante.paterno = "";
        this.postulante.materno = "";

        this.postulante.genero_id = "";
        // this.postulante.pais_id = "";
        this.postulante.departamento_id = "";
        // this.postulante.provincia_id = "";
        // this.postulante.localidad_id = "";

        // this.postulante.grupo_sanguineo_id = "";
        // this.postulante.tiene_discapacidad = "";
        this.postulante.unidad_educativa = "";
        this.postulante.gestion = "";
        this.postulante.correo_electronico = "";
        this.postulante.celular = "";
        this.postulante.direccion = "";
        //this.postulante.codigo_seguro = "";
        // this.postulante.lengua_id = "";
        // this.postulante.pueblo_nacion = "";
        axios
          .post(
            Service.getUrlAPI() + "admision/validar/sie",
            {
              cedula_dni: this.postulante.cedula_dni,
              fecha_nacimiento: this.postulante.fecha_nacimiento,
              complemento: this.postulante.complemento,
            },
            Service.getHeader()
          )
          .then((response) => {
            this.search_loading = false;
            if (response.status === 200) {
              const student = response.data.general;

              this.postulante.informacion_academica = response.data.detalle||"";
              this.postulante.informacion_academica = JSON.stringify(this.postulante.informacion_academica);
              //this.postulante.id = response.data.id;
              this.postulante.nombres = student.nombre.trim();
              this.postulante.paterno = student.paterno || "";
              this.postulante.materno = student.materno || "";
              this.postulante.paterno = this.postulante.paterno.trim();
              this.postulante.materno = this.postulante.materno.trim();
              // this.postulante.genero_id = response.data.genero_id;
              // this.postulante.pais_id = response.data.pais_id;
              // this.postulante.departamento_id = response.data.departamento_id;
              // this.postulante.provincia_id = response.data.provincia_id;
              // this.postulante.localidad_id = response.data.localidad_id;

              // this.postulante.grupo_sanguineo_id = response.data.grupo_sanguineo_id;
              // this.postulante.tiene_discapacidad = response.data.tiene_discapacidad;
              this.postulante.unidad_educativa = student.institucioneducativa || "";
              this.postulante.gestion = student.gestion_tipo_id || "";
              this.postulante.celular = student.celular || "";

              let lengua = this.lenguas.find(lengua => lengua.descripcion === student.idioma);
              this.postulante.lengua_id = lengua ? lengua.id : "";

              let nacion_pueblo = this.nacion_pueblos.find(nacion => nacion.descripcion === student.nacion_originaria);
              this.postulante.nacion_pueblo_id = nacion_pueblo ? nacion_pueblo.id : "";
              /* this.postulante.gestion = response.data.datUnidadEducaitva
                ? response.data.datUnidadEducaitva.gestion
                : ""; */
              // this.postulante.direccion = response.data.direccion;
              // this.postulante.correo_electronico = response.data.correo_electronico;
              // this.postulante.celular = response.data.celular;
              //this.postulante.codigo_seguro = response.data.codigo_seguro;
              // this.postulante.lengua_id = response.data.lengua_id;
              // this.postulante.pueblo_nacion = response.data.pueblo_nacion;
            } else {
              axios
                .post(
                  Service.getUrlAPI() + "admision/validar/persona/egi",
                  {
                    cedula_dni: this.postulante.cedula_dni,
                    fecha_nacimiento: this.postulante.fecha_nacimiento,
                    complemento: this.postulante.complemento,
                  },
                  Service.getHeader()
                )
                .then((response) => {
                  this.search_loading = false;
                  if (response.status === 200) {
                    const student = response.data;
                    if (student != "") {
                      this.postulante.nombres = student.nombres;
                      this.postulante.paterno = student.paterno || "";
                      this.postulante.materno = student.materno || "";
                      this.postulante.celular = student.celular || "";
                      this.postulante.correo_electronico = student.correo_electronico || "";
                      this.postulante.direccion = student.direccion || "";
                      this.postulante.unidad_educativa = student.recorrido_academicos[0].unidad_educativa || "";
                      this.postulante.gestion = student.recorrido_academicos[0].gestion || "";
                      this.postulante.genero_id = student.genero_id || "";
                      this.postulante.departamento_id = student.departamento_id || "";
                    } else {
                      this.toast({ color: "info", text: "Registro no encontrado" });
                    }

                  } else {
                    this.toast({ color: "info", text: "Registro no encontrado" });
                  }
                })
                .catch(() => {
                  this.search_loading = false;
                  console.error("Postulante no encontrado");
                });

            }
          })
          .catch(() => {
            this.search_loading = false;
            console.error("Postulante no encontrado");
          });


      }
    },
