    cargarDatosFotografia() {
      //let extension = this.fFotografia.name.slice(this.fFotografia.name.lastIndexOf('.'));
      //this.cargaConvocatoria();
      this.postulante.tipo = "fotografia"
      this.postulante.descripcion = "fotografia postulacion";
      this.postulante.ruta_imagen = Date.now() + ".png";
      this.postulante.foto = this.fFotografia;
